.services-section {
  padding: 80px 20px;
  background-color: #000;
  color: #fff;
  text-align: center;
  position: relative;
}

.services-section h2 {
  font-size: 34px;
  margin-bottom: 10px;
}

.services-section h4 {
  font-size: 23px;
  margin-bottom: 10px;
  color: #ff8c00;
}

.services-section p {
  font-size: 23px;
  margin-bottom: 20px;
  color: #cacaca;
}

.services-section .service-list {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 40px; 
  position: relative; 
}

.services-section .service-item {
  background-color: #262626;
  padding: 30px;
  border-radius: 10px;
  display: flex; 
  flex-direction: column; 
  align-items: center;  
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  position: relative; 
}

.services-section .service-item h3 {
  font-size: 24px;
  color:#a3a3a3;
}

.services-section .service-item h4 {
  font-size: 20px;
  color: #ff8c00;
}

.services-section .service-item p {
  font-size: 16px;
  line-height: 1.5;
  margin-top: 20px;
  padding: 10px;
  background-color: #333; 
  border-radius: 5px; 
  box-sizing: border-box;
}

.services-section .dot {
  position: absolute;
  left: -15px; 
  top: 50%;
  width: 20px;
  height: 20px;
  background-color: #d46618; 
  border-radius: 50%;
  transform: translateY(-50%); 
}

.services-section .service-list::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 0;
  bottom: 0;
  width: 2px;
  background-color: white; 
  transform: translateX(-50%); 
}


