.about-section {
  padding: 80px 20px;
  background-color: #000; 
  color: #fff;
  text-align: center;
}

.about-section h2 {
  font-size: 34px;
  color: #fff; 
  margin-bottom: 20px;
}

.about-section h4 {
  margin-top: 60px;
  font-size: 23px;
  color: #ff8c00; 
  margin-bottom: 10px;
}
.about-section p {
  color: #c1c1c1; 
  font-size: 18px;
}

.arrow {
  text-align: center;
  font-size: 30px;
  display: block;
  margin-top: 20px;
}
.subtitle {
  font-size: 24px;
  margin-bottom: 50px;
  color: #fff; 
}

.info-sections {
  max-width: 1200px;
  display: block;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 0 auto; 
  margin-top: 40px;
}

.info-part {
  background-color: #262626; 
  border-radius: 10px; 
  padding: 20px;
  margin-bottom: 20px; 
  text-align: left;
  flex: 600px;
}

.info-container {
  display: flex;
  align-items: center; /* 세로 정렬을 가운데로 맞춤 */
  gap: 10px; /* 요소 간의 간격 조절 */
  margin-bottom: 10px;
}

.number {
  background-color: #3c2e24;
  color: #ff8c00;
  padding: 5px 10px;
  border-radius: 8px;
  display: inline-block;
  margin-bottom: 0; /* 하단 여백 제거 */
  font-weight: bold;
}

.about-section h5 {
  margin-top: 0; /* 상단 여백 제거 */
  font-size: 20px;
  color: white;
  margin-bottom: 1; /* 하단 여백 제거 */
}

/* 공모 컨테이너 */
.tender-container {
  background-color: #333; 
  padding: 20px;
  width: 100%;
  height: auto; /* 높이를 자동으로 조정 */
  border-radius: 10px;
  overflow: hidden; 
  position: relative;
  display: flex;
  align-items: center; /* 수직 가운데 정렬 */
}


